.item {
    margin: 1rem 0;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    font-weight: bold;
    padding: 1rem;
    background-color: #f7f5ef;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
  }
  
  .item button {
    background-color: rgba(0, 0, 0, 0.658);
    display: inline-block;
    border: 2px solid black;
    color: white;
    padding: 2px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .item button:hover {
    background-color: rgb(248, 10, 10);
  }